(function () {
    'use strict';
    
    var app = angular.module('routes', ['ui.router']);

    app.config(function ($stateProvider, $urlRouterProvider) {

        $urlRouterProvider.otherwise('/');

        var router = $stateProvider;

        router.state('index', {
            url: "/",
            templateUrl: "/app/partials/index.html"
        });
    });
})();